@import url(https://fonts.googleapis.com/css?family=Lobster);
.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 2rem; }
  .card-container::after {
    content: '';
    flex: auto; }
  .card-container .card {
    margin: 1.25rem 0.75rem;
    width: 232px; }
    .card-container .card .release-date-wrapper {
      position: absolute;
      height: 300px;
      width: 232px;
      display: flex;
      justify-content: flex-end; }
      .card-container .card .release-date-wrapper .release-date {
        border-radius: 50%;
        padding: 0.5rem;
        background: #00a341;
        font-size: 0.625rem;
        text-align: center;
        height: 2rem;
        width: 2rem;
        margin: 0.5rem;
        font-weight: 500; }
    .card-container .card .play-icon {
      position: absolute;
      height: 300px;
      width: 232px;
      display: flex;
      justify-content: center;
      align-items: center; }
    .card-container .card .expand-view {
      display: flex;
      width: 100vw;
      padding: 1rem 2.75rem;
      margin: 1rem 0;
      box-sizing: border-box;
      position: absolute;
      left: 0;
      cursor: auto; }
      .card-container .card .expand-view .video {
        flex: 1 1; }
        .card-container .card .expand-view .video iframe {
          border: none;
          height: 25rem;
          width: 100%;
          flex: 1 1; }
      .card-container .card .expand-view .expanded-desc {
        flex: 1 1;
        background: rgba(255, 255, 255, 0.1);
        width: 100%;
        height: 25rem;
        margin-left: 2rem; }
        .card-container .card .expand-view .expanded-desc .title,
        .card-container .card .expand-view .expanded-desc .lang,
        .card-container .card .expand-view .expanded-desc .genre,
        .card-container .card .expand-view .expanded-desc .vote-and-date,
        .card-container .card .expand-view .expanded-desc .watch {
          margin: 1rem; }
        .card-container .card .expand-view .expanded-desc .lang {
          color: #c0c0c0; }
        .card-container .card .expand-view .expanded-desc .vote-and-date,
        .card-container .card .expand-view .expanded-desc .watch {
          font-size: 0.75rem;
          display: flex; }
          .card-container .card .expand-view .expanded-desc .vote-and-date .vote,
          .card-container .card .expand-view .expanded-desc .vote-and-date .date,
          .card-container .card .expand-view .expanded-desc .watch .vote,
          .card-container .card .expand-view .expanded-desc .watch .date {
            margin-right: 2rem;
            display: flex;
            font-size: 0.625rem;
            line-height: 1rem; }
            .card-container .card .expand-view .expanded-desc .vote-and-date .vote .text-block .first-line,
            .card-container .card .expand-view .expanded-desc .vote-and-date .date .text-block .first-line,
            .card-container .card .expand-view .expanded-desc .watch .vote .text-block .first-line,
            .card-container .card .expand-view .expanded-desc .watch .date .text-block .first-line {
              font-weight: 500; }
            .card-container .card .expand-view .expanded-desc .vote-and-date .vote .text-block .second-line,
            .card-container .card .expand-view .expanded-desc .vote-and-date .date .text-block .second-line,
            .card-container .card .expand-view .expanded-desc .watch .vote .text-block .second-line,
            .card-container .card .expand-view .expanded-desc .watch .date .text-block .second-line {
              color: #c0c0c0; }
        .card-container .card .expand-view .expanded-desc .watch {
          height: 150px;
          align-items: flex-end;
          justify-content: space-around;
          text-align: center;
          font-weight: 400;
          line-height: 1.25rem; }
          .card-container .card .expand-view .expanded-desc .watch .will {
            color: #00a341; }
          .card-container .card .expand-view .expanded-desc .watch .maybe {
            color: #f09800; }
          .card-container .card .expand-view .expanded-desc .watch .wont {
            color: #bd0300; }
        .card-container .card .expand-view .expanded-desc .title {
          font-size: 1.5rem;
          font-weight: 500; }

.header-container {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1000;
  left: 0;
  top: 0; }
  .header-container .header {
    padding: 1rem 2.75rem;
    background: #000000;
    display: flex;
    box-sizing: border-box; }
    .header-container .header .title {
      font-size: 2rem;
      font-family: Lobster;
      flex: 1 1; }
    .header-container .header .selects {
      flex: 1 1;
      display: flex;
      justify-content: flex-end; }
  .header-container .sub-header {
    padding: 0.5rem 2.75rem;
    display: flex;
    box-sizing: border-box;
    background: #666666;
    align-items: center; }

.app {
  color: #ffffff; }

.center {
  display: flex;
  align-items: center;
  justify-content: center; }

.no-data-center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh; }

.pointer {
  cursor: pointer; }

.img-border {
  border: 0.25rem solid #00a341; }

body {
  margin: 0;
  font-family: Roboto;
  color: #ffffff;
  background: #1a1a1a; }

